.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


body{
  background-color: rgb(0, 0, 0);
  width: auto;
  height: auto;
  position: sticky;
  background-attachment: fixed;
  background-size: 100%;
  font-family: 'Open Sans', sans-serif ;
  font-size: 16px;
  color: white;
  line-height: 20px;
  
}

.columnas{
  overflow: hidden;
}


.holder {
  width: 960px;
  margin: 0 auto;
}

h2 {
  color: #2e54a2;
  font-weight: 400;
  margin: 0 0 1em;
}


